<template>
  <div>
    <div class="cal-frame">
      <!-- result part -->
      <div class="result-frame">
        <div class="res-content">
          <div class="solution">
            <input v-model="solution" type="text" disabled />
          </div>
          <br />
          <div class="result">
            =
            <div class="answer">
              <input v-model="answer" type="text" disabled />
            </div>
          </div>
        </div>
      </div>
      <!-- end of result part -->
      <div class="components">
        <!-- <v-btn color="#6bb6f3" @click="modulo">%</v-btn> -->
        <div>
          <v-btn color="#f8782d" @click="clearAll">CA</v-btn>
          <v-btn @click="remove"
            ><v-icon color="#f8782d">mdi-backspace-outline</v-icon></v-btn
          >
        </div>
        <div>
          <v-btn @click="solution7">7</v-btn>
          <v-btn @click="solution8">8</v-btn>
          <v-btn @click="nine">9</v-btn>
          <v-btn @click="divide"
            ><v-icon color="#f8782d">mdi-slash-forward</v-icon></v-btn
          >
        </div>
        <div>
          <v-btn @click="six">6</v-btn>
          <v-btn @click="five">5</v-btn>
          <v-btn @click="four">4</v-btn>
          <v-btn @click="product"
            ><v-icon color="#f8782d">mdi-close</v-icon></v-btn
          >
        </div>
        <v-btn @click="three">3</v-btn>
        <v-btn @click="two">2</v-btn>
        <v-btn @click="one">1</v-btn>
        <v-btn @click="subtract"
          ><v-icon color="#f8782d">mdi-minus</v-icon></v-btn
        >
        <div>
          <v-btn @click="zero">0</v-btn>
          <v-btn @click="point">.</v-btn>
          <v-btn color="#f8782d" @click="equals">=</v-btn>
          <v-btn @click="add"><v-icon color="#f8782d">mdi-plus</v-icon></v-btn>
        </div>
        <!-- end of components -->
      </div>

      <!-- end cal frame -->
    </div>
  </div>
</template>
<script>
export default {
  name: "calcu-lator",
  data() {
    return {
      solution: "",
      answer: 0,
    };
  },
  methods: {
    solution7() {
      this.solution = this.solution + "7";
      this.answer = eval(this.solution);
    },
    solution8() {
      this.solution = this.solution + "8";
      this.answer = eval(this.solution);
    },
    nine() {
      this.solution = this.solution + "9";
      this.answer = eval(this.solution);
    },
    six() {
      this.solution = this.solution + "6";
      this.answer = eval(this.solution);
    },
    five() {
      this.solution = this.solution + "5";
      this.answer = eval(this.solution);
    },
    four() {
      this.solution = this.solution + "4";
      this.answer = eval(this.solution);
    },
    three() {
      this.solution = this.solution + "3";
      if (this.solution == "143") {
        this.answer = "I Love You Ericka";
      } else {
        this.answer = eval(this.solution);
      }
    },
    two() {
      this.solution = this.solution + "2";
      this.answer = eval(this.solution);
    },
    one() {
      this.solution = this.solution + "1";
      this.answer = eval(this.solution);
    },
    zero() {
      this.solution = this.solution + "0";
      this.answer = eval(this.solution);
    },
    point() {
      this.solution = this.solution + ".";
      this.answer = eval(this.solution);
    },
    divide() {
      this.solution = this.solution + "/";
      this.answer = eval(this.solution);
    },
    product() {
      this.solution = this.solution + "*";
      this.answer = eval(this.solution);
    },
    subtract() {
      this.solution = this.solution + "-";
      this.answer = eval(this.solution);
    },
    add() {
      this.solution = this.solution + "+";
      this.answer = eval(this.solution);
    },
    // modulo() {
    //   this.answer = eval("%" + this.solution);
    // },
    equals() {
      console.log(this.solution);
      this.answer = eval(this.solution);
    },
    remove() {
      this.solution = this.solution.slice(0, -1);
      if (this.solution == "143") {
        this.answer = "I Love You So Much Ericka";
      } else {
        this.answer = eval(this.solution);
      }
      console.log(this.solution);
    },
    clearAll() {
      this.solution = "";
      this.answer = 0;
    },
  },
};
</script>
<style scoped>
.cal-frame {
  width: 350px;
  height: 400px;
  margin-top: 50px;
  background-color: rgb(179, 171, 171);
  border-radius: 20px;
  padding: 35px;
  border: none;
  box-shadow: 5px 3px 3px #4d4b4b;
}
/* result style */
.result-frame {
  height: 70px;
  width: 100%;
  background-color: whitesmoke;
  border-radius: 10px;
  box-shadow: 3px 2px 2px #535252;
  padding: 10px;
}
.solution {
  float: right;
}
.answer {
  float: right;
}
.result {
  margin-top: 10px;
}
input {
  /* border: none; */
  text-align: right;
}
/* end of result style */

/* components styles */
.components {
  margin-top: 30px;
}
.v-btn__content {
  font-weight: bold;
}
button {
  margin-right: 6px;
  margin-bottom: 10px;
}
/* end of components styles */
/* mobile view */
@media only screen and (max-width: 400px) {
  .cal-frame {
    width: 300px;
  }
  .v-btn:not(.v-btn--round).v-size--default {
    min-width: 50px;
  }

  .result {
    margin-top: 5px;
  }
}
</style>